@import url("https://fonts.googleapis.com/css2?family=Pixelify+Sans:wght@400;600&display=swap");

* {
  margin: 0;
}

.header {
  height: 20px;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  text-align: center;
  font-family: "Pixelify Sans", cursive;
  font-weight: 600;
}
.gradient-1 {
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(71, 209, 85, 1) 0%,
    rgba(121, 9, 102, 1) 100%,
    rgba(249, 69, 196, 1) 100%
  );
}
.gradient-2 {
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(43, 240, 63, 1) 0%,
    rgba(208, 84, 171, 1) 100%,
    rgba(121, 9, 102, 1) 100%
  );
}
.gradient-3 {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(43, 240, 63, 1) 0%,
    rgba(31, 161, 43, 1) 3%,
    rgba(163, 14, 119, 0.6923144257703081) 100%,
    rgba(121, 9, 102, 1) 100%
  );
}
.gradient-4 {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(13, 14, 27, 1) 0%,
    rgba(39, 44, 120, 1) 0%,
    rgba(15, 19, 98, 0.5690651260504201) 100%,
    rgba(121, 9, 102, 1) 100%
  );
}
.gradient-5 {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(13, 14, 27, 1) 0%,
    rgba(8, 10, 38, 1) 0%,
    rgba(15, 19, 98, 0.5690651260504201) 100%,
    rgba(121, 9, 102, 1) 100%
  );
}
.gradient-6 {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(13, 14, 27, 1) 0%,
    rgba(8, 10, 38, 1) 0%,
    rgba(15, 19, 98, 0.6923144257703081) 100%,
    rgba(121, 9, 102, 1) 100%
  );
}
body {
  height: 100%;
  background: rgba(2, 0, 36, 1) 0%;
}
.home {
  font-family: "Pixelify Sans", cursive;
  color: wheat;
  margin: 50px auto;
}
.tab {
  margin-left: 10px;
}
.services {
  border-width: 4px;
  border-style: solid;
  border-image: linear-gradient(to right, rgb(12, 199, 75), rgb(211, 24, 195)) 1;
  width: max-content;
  padding: 10px;
}
.services-head {
  font-size: 30px;
}
